@import url("https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300");

.App {
  font-family: "Open Sans Condensed", sans-serif;
}

.App .big-title {
  text-align: center;
  padding: 10px;
}

.App .big-title h1,
h1 {
  color: #000;
  letter-spacing: 4px;
  font-family: "Open Sans Condensed", sans-serif;
  border: 1px solid #000;
  padding: 3px;
}

.App .echarts-component {
  margin: 3px 5px;
  padding: 10px;
  -webkit-box-shadow: -2px -1px 34px -3px rgba(0, 0, 0, 0.73);
  box-shadow: -2px -1px 34px -3px rgba(0, 0, 0, 0.73);
}

.App .echarts-component h1 {
  margin-top: 10px;
  text-align: center;
  letter-spacing: 2px;
}

.App .randomize {
  position: fixed;
  right: 0;
  top: 15%;
  z-index: 9999;
  width: 100px;
  background: #000;
  font-size: 14px;
  color: #c9dbe2;
  opacity: 0.7;
}

.App .randomize:hover {
  background: #000;
  color: #c9dbe2;
  opacity: 1;
}

.App .control-buttons {
  margin-top: 30px;
  margin-bottom: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

pre {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  font-size: 13px;
}



